<template>
  <Plotly :data="data" :layout="layout" :display-mode-bar="true"></Plotly>
</template>
<script>
import figure from './3d-ribbon.json'
export default {
  data () {
    return {
      data: [],
      layout: {
        title: 'Ribbon Plot',
        showlegend: false,
        autosize: true,
        // width: 600,
        // height: 600,
        scene: {
          xaxis: { title: 'Sample #' },
          yaxis: { title: 'Wavelength' },
          zaxis: { title: 'OD' }
        }
      }
    }
  },
  mounted () {
    figure.data.forEach(data => {
      this.data.push({
        x: data.x,
        y: data.y,
        z: data.z,
        name: '',
        colorscale: data.colorscale,
        type: 'surface',
        showscale: false
      })
    })
  }
}
</script>
